"use client";
import { CalendarDays, Music, Trophy, Code, Users } from 'lucide-react';
import { useState } from 'react';
import { motion } from 'framer-motion';
import { JSX } from 'react';

// أنواع الأحداث مع أيقوناتها
const eventTypes: { [key: string]: { icon: JSX.Element; color: string } } = {
    Musical: { icon: <Music className="w-5 h-5" />, color: 'text-purple-600' },
    Competition: { icon: <Trophy className="w-5 h-5" />, color: 'text-blue-600' },
    Coding: { icon: <Code className="w-5 h-5" />, color: 'text-green-600' },
    Conference: { icon: <Users className="w-5 h-5" />, color: 'text-pink-600' }
};

const events = [
    {
        date: "11",
        month: "MAY",
        title: "Civil Padura",
        department: "Civil Engineering Department",
        type: "Musical",
        details: ["Musical Event", "All universities students can join"],
        university: "University of Moratuwa"
    },
    {
        date: "14",
        month: "MAY",
        title: "Yaye Padura",
        department: "Chemical Department",
        type: "Musical",
        details: ["Musical Event", "All universities students can join"],
        university: "University of Moratuwa"
    },
    {
        date: "APR",
        month: "25",
        title: "Devthon",
        department: "Leo Club UOM",
        type: "Competition",
        details: ["Web designing competition", "All UOM students can join"],
        university: "University of Moratuwa"
    }
];

export default function UpcomingEvents() {
    const [filter, setFilter] = useState('all');

    return (
        <section className="bg-gray-50 py-12">
            <div className="max-w-7xl mx-auto px-4">
                {/* العنوان وأزرار التصفية */}
                <div className="flex flex-col md:flex-row justify-between items-center mb-8">
                    <h2 className="text-3xl font-bold text-gray-800 mb-4 md:mb-0">Upcoming Events</h2>
                    <div className="flex gap-4">
                        {['Weekdays', 'Popular', 'Latest'].map((btn) => (
                            <motion.button
                                key={btn}
                                whileHover={{ scale: 1.05 }}
                                whileTap={{ scale: 0.95 }}
                                onClick={() => setFilter(btn.toLowerCase())}
                                className={`px-4 py-2 rounded-full ${filter === btn.toLowerCase()
                                    ? 'bg-purple-600 text-white'
                                    : 'bg-white text-gray-600 border border-gray-200'
                                    }`}
                            >
                                {btn}
                            </motion.button>
                        ))}
                    </div>
                </div>

                {/* بطاقات الأحداث */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {events.map((event, idx) => (
                        <motion.div
                            key={idx}
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ delay: idx * 0.1 }}
                            className="bg-white rounded-xl shadow-lg overflow-hidden hover:shadow-xl transition-shadow"
                        >
                            {/* رأس البطاقة */}
                            <div className="p-4 bg-gradient-to-r from-purple-50 to-blue-50">
                                <div className="flex items-center gap-2 mb-2">
                                    <span className={`${eventTypes[event.type].color}`}>
                                        {eventTypes[event.type].icon}
                                    </span>
                                    <span className="text-sm font-semibold text-gray-600">
                                        {event.department}
                                    </span>
                                </div>
                                <h3 className="text-xl font-bold text-gray-800">{event.title}</h3>
                            </div>

                            {/* محتوى البطاقة */}
                            < div className="p-4" >
                                <div className="flex items-center gap-2 mb-4">
                                    <CalendarDays className="w-5 h-5 text-gray-400" />
                                    <div className="flex gap-1">
                                        <span className="font-semibold">{event.date}</span>
                                        <span className="text-gray-500">{event.month}</span>
                                    </div>
                                </div><div className="space-y-2 mb-4">
                                    {event.details.map((detail, i) => (
                                        <div key={i} className="flex items-center text-gray-600">
                                            <span className="mr-2">⬥</span>
                                            {detail}
                                        </div>
                                    ))}
                                </div>

                                <div className="text-sm text-gray-500 font-medium">
                                    {event.university}
                                </div>
                            </div>
                        </motion.div>
                    ))
                    }
                </div >

                {/* زر عرض جميع الأحداث */}
                < div className="text-center mt-8" >
                    <motion.button
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                        className="bg-purple-600 text-white px-6 py-3 rounded-full hover:bg-purple-700 transition-colors"
                    >
                        See All Events
                    </motion.button>
                </div >
            </div >
        </section >
    );
}